import React from "react";

class Drawer extends React.Component {
  render() {
    let drawerConfig = this.props.drawerConfig;
    let style;
    if (this.props.isMenuOpened) {
      style = {
        left: "0px",
        transition: "all .5s cubic-bezier(.3,-0.08,.76,1.2)",
        backfaceVisibility: "hidden"
      };
    }
    if (!this.props.isMenuOpened) {
      style = {
        left: "-300px",
        transition: "all .5s cubic-bezier(.3,-0.08,.76,1.2)",
        backfaceVisibility: "hidden"
      };
    }
    return (
      <div className={drawerConfig.className} style={{ ...style }}>
        {this.props.children}
      </div>
    );
  }
}

export default Drawer;
